<template>
	<div class="todolist">
		<div id="add" class="center">
			<h1>TodoList</h1><input ref="itemInput" v-focus v-model="newitem.it" type="text" id="thing" @keyup.enter="creatitem"/>
			<input @click="creatitem()" id="addit" class="btn" type="button" value="添加" />
		</div>
		<div>
			<table id="list">
				<tr v-for="(item,index) in items">
					<td class="it" v-if="!item.isEdit" v-bind:class="{'finished':item.finished}"><p>{{item.it}}</p></td>
					<td><input @click="item.finished = true" class="btn" type="button" value="finish" /></td>
					<td><input @click="delitem(index)" class="btn" type="button" value="del" /></td>
				</tr>
			</table>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	Vue.directive('focus', {
		// 当绑定元素插入到 DOM 中。
		inserted: function(el) {
			// 聚焦元素
			el.focus()
		}
	})
	export default {
		name: 'todolist',
		data() {
			return {
				newitem: {
					it: "",
					finished: false,
					isEdit: false
				},
				items: [{
					it: 'a',
					finished: true,
					isEdit: false
				}],
			}
		},
		methods: {
			creatitem: function() {
				if(!this.newitem.it){
					return false;
				}
				this.items.push(this.newitem);
				this.newitem = {
					it: "",
					finished: false
				};
				document.getElementById("thing").focus();
			},
			delitem: function(idx) {
				this.items.splice(idx, 1);
			}
		},
		watch: {
			items: {
				handler: function() {
					if(window.localStorage) {
						window.localStorage.setItem('todoItems', JSON.stringify(this.items));
						console.log('记录待办事项');
					}
				},
				deep: true
			}
		},
		created() {
			if(window.localStorage.getItem('todoItems')) {
				this.items = JSON.parse(window.localStorage.getItem('todoItems'));
				console.log('读取待办事项');
			}
		}
	}
</script>

<style scoped>
	.center {
		width: 170px;
		margin: 0 auto;
		margin-top: 60px;
	}

	#thing {
		width: 100px;
		margin-right: 10px;
	}

	.btn {
		border: none;
		padding: 5px;
		border-radius: 5px;
		background-color: #009bdb;
		color: #fff;
	}

	#list {
		width: 400px;
		margin: 0 auto;
		margin-top: 60px;
	}

	.num {
		width: 50px;
	}

	.it {
		display: block;
		width: 500px;
		overflow: inherit;
	}

	td {
		width: 50px;
		height: 28px;
	}

	.finished {
		text-decoration: line-through;
	}
</style>


